d;\n        border-top-color: currentcolor;\n        border-right: 2px solid;\n        border-right-color: currentcolor;\n        border-color: #fff;\n        -webkit-transform: rotate(45deg);\n        transform: rotate(45deg);\n    }\n    #tour-info .vfc-content {\n        margin: 0 20px;\n        margin-bottom: 0px;\n        z-index: 1;\n    }\n    .vfc-popover-container {\n        width: 100%;\n        max-width: 300px;\n    }\n\n</style>\n"],"sourceRoot":""}]);
